import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

class ValidationError {
  show: boolean
  meesages: any
}

@Injectable({
  providedIn: 'root'
})
export class ValidationErrorService {
  private validationError = new ValidationError();
  private validationErrorSubject = new Subject<ValidationError>();
  public validationErrorState = this.validationErrorSubject.asObservable()
  constructor() { }

  public setValidationError(validationErrors: any) {
    this.validationError.show = true;
    this.validationError.meesages = validationErrors;
    this.validationErrorSubject.next(this.validationError)
  }

  public clearValidationError() {
    this.validationError.show = false;
    this.validationError.meesages = null;
    this.validationErrorSubject.next(this.validationError);
  }
}
