import { Injectable } from '@angular/core';
import { ShHttpClientService } from './sh-http-client.service';
import { Router, NavigationEnd } from '@angular/router';


type logParam = {
  path: string
  action?: string
}
type config = {
  url :string
  useHash: boolean
}

@Injectable({
  providedIn: 'root'
})
export class LoggerService {
  private isBackButton = false;
  private config: config = {url: '', useHash: true};
  constructor(
    private http: ShHttpClientService,
    private router: Router,
  ) { }
  /**
   * logApiã¸ãªã¯ã¨ã¹ã
   * 
   */
  private async push(param: logParam) {
    return this.http.post(this.config.url, param);
  }
  /**
   * è¨­å®ãæ´æ°
   */
  public configure(config : Partial<config>) {
    const { url, useHash } = config;
    if(url) {
      this.config.url = url;
    }
    if(useHash !== undefined && useHash !== null) {
      this.config.useHash = useHash;
    }
    return this;
  }
  /**
   * ç»é¢é·ç§»ãçºçãããã³ã«logging
   */
  public trackingPath() {
    this.router.events.subscribe(async (event) => {
      if(event instanceof NavigationEnd){
        if(this.isBackButton){
          this.isBackButton = false;
          return;
        }
        const path = this.config.useHash 
          ? `#${event.urlAfterRedirects}`
          : event.urlAfterRedirects.replace('/', '');
        const result = await this.push({path});
      }
    });
  }
  /**
   * actionlog pathã¯å¿é ã®ããå¿ãéããã
   */
  public async action(action: string) {
    const path = this.config.useHash
      ? `#${this.router.url}`
      :  this.router.url.replace('/', '');
    await this.push({path, action});
  }
  /**
   * 
   */
  public stopLoggingPath() {
    this.isBackButton = true;
  }
  
}
