import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ShHttpClientConst, Response } from '../constShHttpClient';
import { ShHttpClientService } from './sh-http-client.service';
import { ApiResponseBody } from '../utilities/ApiResponseBody';
import { async } from '@angular/core/testing';
import { ApiResponse } from '../utilities/ApiResponse';


@Injectable({
  providedIn: 'root'
})
export class VersionService {

  //versionã®ã­ã£ãã·ã¥
  private versions: { keyUrl: string, version: string }[] = [];

  constructor(
    private http: HttpClient
  ) { }

  /**
   * versionã®åæå
   */
  public async initVersion(...urls: string[]) {
    if (!urls) return;
    await Promise.all(urls.map(async (url) => {
      const result = await this.fetchVersion(url)
      this.setVersionNo(url, result)
      return
    }))
  }

  /**
   * urlããã­ã¼ãçæ
   * @param url 
   */
  private getKeyUrl(url: string) {
    if (!url) return "";
    return url.split(ShHttpClientConst.API_URL)[0] + ShHttpClientConst.VERSION_API_URL;
  }
  /**
   * ããã¯ã¨ã³ãã®versionæå ±ãåå¾
   */
  public async fetchVersion(url: string): Promise<string> {
    const s = this.splitByApiUrl(url);
    const token = sessionStorage.getItem(`${ShHttpClientConst.AUTHORIZATION_TOKEN}`)
    return this.http.get<ApiResponseBody<any>>(s, { observe: Response, headers: { "Authorization": token ? ShHttpClientConst.Bearer + token : ShHttpClientConst.Bearer } }).toPromise()
      .then(response => {
        if (response.body.redirectURL == ShHttpClientConst.RELOAD) {
          setTimeout(function(){location.reload();}, 100);

        }
        else if ((response.body.redirectURL || "").indexOf(ShHttpClientConst.HTTP) != -1) {
          setTimeout(function(){location.href = response.body.redirectURL;}, 100);
        }
        return response.body.applicationData ? response.body.applicationData.versionNo : "";
      });
  }

  /**
   * urlãsplitããå¤ãè¿ã
   */
  private splitByApiUrl(url: string) {
    return url.split(ShHttpClientConst.API_URL)[0] + ShHttpClientConst.VERSION_API_URL || '';
  }
  /**
   * ã­ã£ãã·ã¥ãã¦ããversionãURLããåå¾ãã
   * @param url
   */
  public async getVersion(url: string): Promise<string> {
    const keyUrl = this.getKeyUrl(url)
    const projectVersion = this.versions
      .find(value => {
        return value.keyUrl === keyUrl
      })
    if (!projectVersion) {
      const version = await this.fetchVersion(url)
      // //ã­ã£ãã·ã¥ãã
      this.setVersionNo(url, version)
      return version;
    }
    return projectVersion.version;

  }

  /**
   * åãåã£ãversionNoãã­ã£ãã·ã¥ãã
   * @param url apiUrl
   * @param versionNo ããã¯ã¨ã³ãã®version 
   */
  public setVersionNo(url: string, versionNo: string) {
    const keyUrl = this.getKeyUrl(url);
    if (!keyUrl || !versionNo) return
    const result = this.versions.findIndex(item => item.keyUrl == keyUrl);
    if(result != -1) {
      this.versions[result] = { keyUrl, version: versionNo };
    }else {
      this.versions.push({ keyUrl, version: versionNo });
    }
  }
}
