import { Injectable } from '@angular/core';
import { Observable } from 'rxjs'
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { catchError, map } from 'rxjs/operators'
import { ApiResponse } from '../utilities/ApiResponse';
import { VersionService } from './version.service';
import { AlertHandlerService } from './alert-handler.service';
import { ValidationErrorService } from './validation-error.service';
import { ApiResponseBody } from '../utilities/ApiResponseBody';
import { ShHttpClientConst, Response } from '../constShHttpClient';
import { SHMessage } from '../utilities/ShMessage';
import { ShRedirectService } from './sh-redirect.service';

@Injectable({
  providedIn: 'root'
})
export class ShHttpClientService {

  constructor(
    private http: HttpClient,
    private versionService: VersionService,
    private alertHandlerService: AlertHandlerService,
    private validationErrorService: ValidationErrorService,
    private redirectService: ShRedirectService
  ) { }

  /**
   * AccessTokenã®æç¡ãè¿ã
   */
  get hasToken(): boolean {
    return !!sessionStorage.getItem('AccessToken');
  }
  /**
   * httpOptionãè¿ã
   * @param versionNo 
   */
  private getOption(versionNo: string): object {
    const token = sessionStorage.getItem(`${ShHttpClientConst.AUTHORIZATION_TOKEN}`)
    let headers = new HttpHeaders({ "Version-No": versionNo })
    headers = headers.set('Content-Type', 'application/json; charset=utf-8');
    const accessToken = token ? ShHttpClientConst.Bearer + token : ShHttpClientConst.Bearer;
    headers = headers.set('Authorization', accessToken);
    return { observe: Response, headers };
  }
  /**
  * GET
  * @param apiResponseBody ã¬ã¹ãã³ã¹Bodyæå ±
  * @return ã¬ã¹ãã³ã¹Bodyåã®bodyå¥
  */
  private getApiResponseBody<T>(apiResponseBody: ApiResponse<T>, showValidationResult: boolean): ApiResponseBody<T> {
    if (apiResponseBody.status != ShHttpClientConst.STATUS_OK)
      return new ApiResponseBody<T>();
    if (apiResponseBody.body) {
      if (apiResponseBody.body.message) {
        alert(apiResponseBody.body.message);
      }
      if (apiResponseBody.body.message) {
        alert(apiResponseBody.body.message);
      }
      if (apiResponseBody.body.redirectURL == ShHttpClientConst.RELOAD) {
        setTimeout(function(){location.reload();}, 100);
      }
      else if ((apiResponseBody.body.redirectURL || "").indexOf(ShHttpClientConst.HTTP) != -1) {
        // useHashãtrueã«ãªã£ã¦ãããã¨ãåææ¡ä»¶ã§ã
        this.redirectService.setRedirectPath();
        setTimeout(function(){location.href = apiResponseBody.body.redirectURL}, 100);
      }
      if(showValidationResult) {
        //ããªãã¼ã·ã§ã³å¦ç
        if (apiResponseBody.body.hasValidationErrors) {
          this.validationErrorService.setValidationError(apiResponseBody.body.validationErrors);
        } else {
          this.validationErrorService.clearValidationError();
        }
      }
      
      if (apiResponseBody.body.alert) {
        //ã¢ã©ã¼ãå¦ç
        this.alertHandlerService.setAlert(apiResponseBody.body.alert);
      }
    }

    return apiResponseBody.body;
  }
  /**
   * èªè¨¼æ¸ã¿ãã©ãã
   */
  private isAuthenticate(): boolean {
    return !!sessionStorage.getItem(`${ShHttpClientConst.AUTHORIZATION_TOKEN}`)
  }
  /**
  * GET
  * @param url ApiUrl
  * @return APIè¿å´çµæ
  */
  public async get<T>(url: string, showValidationResult: boolean = true): Promise<ApiResponseBody<T>> {
    let versionNo = ""
    if (this.isAuthenticate()) {
      versionNo = await this.versionService.getVersion(url);
    }
    const option: object = this.getOption(versionNo);
    return this.http.get<ApiResponse<T>>(this.encodeUrl(url), option).pipe(
      map(response => {
        return this.getApiResponseBody<T>(response, showValidationResult);
      }),
      catchError(this.handleError<any>("get error"))
    ).toPromise();
  }

  /**
  * POST
  * @param url ApiUrl
  * @param data ãã¼ã¿
  * @return APIè¿å´çµæ
  */
  public async post<T>(url: string, data: any, showValidationResult: boolean = true): Promise<ApiResponseBody<T>> {
    const versionNo = await this.versionService.getVersion(url);
    const option: object = this.getOption(versionNo)
    return this.http.post<ApiResponse<T>>(url, data, option).pipe(
      map(response => {
        return this.getApiResponseBody(response, showValidationResult);
      }),
      catchError(this.handleError<any>("post error"))
    ).toPromise();

  }

  /**
  * PUT
  * @param url ApiUrl
  * @param data ãã¼ã¿
  * @return APIè¿å´çµæ
  */
  public async put<T>(url: string, data: any, showValidationResult: boolean = true): Promise<ApiResponseBody<T>> {
    const versionNo = await this.versionService.getVersion(url);
    const option: object = this.getOption(versionNo)
    return this.http.put<ApiResponse<T>>(url, data, option).pipe(
      map(response => {
        return this.getApiResponseBody(response, showValidationResult);
      }),
      catchError(this.handleError<any>("put error"))
    ).toPromise();

  }

  /**
  * DELETE
  * @param url ApiUrl
  * @return APIè¿å´çµæ
  */
  public async delete<T>(url: string, showValidationResult: boolean = true): Promise<ApiResponseBody<T>> {
    const versionNo = await this.versionService.getVersion(url);
    const option: object = this.getOption(versionNo)
    return this.http.delete<ApiResponse<T>>(url, option).pipe(
      map(response => {
        return this.getApiResponseBody(response, showValidationResult);
      }),
      catchError(this.handleError<any>("delete error")),
    ).toPromise();

  }

  /**
   * ã¨ã©ã¼ãã³ãã©
   * @param operation ã©ã®å¦çã§ã¨ã©ã¼ãèµ·ããã
   * @param result ç©ºã®éåãæå®ç¡ããè¿ãã¦å¦çãç¶ç¶ããã
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      const msg: SHMessage = new SHMessage();
      alert(msg.fatal.systemErrorHasOccurred());
      throw error;
    };

  }
  /**
   * urlãã¨ã³ã³ã¼ããããã¤ã³ã¿ã¼ã¹ãã¼ã¸ç¨ã®å¯¾å¿
   * @param url 
   */
  private encodeUrl(url: string): string {
    if (!url) return;
    if (url.indexOf('?')) {
      return encodeURI(url);
    }
    return url;
  }
}
